import React from 'react'
import { YMaps, Map as YMap, FullscreenControl, Placemark } from 'react-yandex-maps';

const PLACES = [{
  geometry: [44.95, 34.1],
  iconContent: 'F',
  iconCaption: 'Симферополь',
  hintContent: 'hint',
  balloonContentHeader: 'header',
  balloonContentBody: 'body',
  balloonContentFooter: 'footer',
}];

const Map = () => (
  <YMaps>
    <YMap
      defaultState={{
        center: [44.95, 34.1],
        zoom: 12,
      }}
      options={{ fullscreenZIndex: 0 }}
    >
      <FullscreenControl
        options={{ visible: false }}
        instanceRef={ref => ref && ref.enterFullscreen()}
      />
      {PLACES.map(({ geometry, ...properties }) => <Placemark
        geometry={geometry}
        properties={properties}
        modules={['geoObject.addon.balloon', 'geoObject.addon.hint']}
      />)}

    </YMap>
  </YMaps >
)

export default Map;
